<template>
  <div>
    <cd-doc :content="content"></cd-doc>
    <cd-info v-for="(info, index) in select" :key="index" :component="info" property="props"></cd-info>
    <select v-model="selected" v-on:change="onpropertyselect">
      <option v-for="(option) in examples" :key="option.id" :value="option.id" :label="option.name">{{ option.name }}</option>
    </select>
    <cd-prop-example v-if="property">
      <cd-form slot="editor" :descriptor="options" :payload="property.payload" :onpropertychange="onpropertychange(property.payload)"></cd-form>
      <div slot="preview">
        <code>{{ property.payload }}</code>
        <div v-if="property.params">
          <h3> !</h3>
          <div>       ,    </div>
          <cd-form :descriptor="property.paramsdescriptor" :payload="property.params" :onpropertychange="onpropertychange(property.params)"></cd-form>
        </div>
      </div>
      <cd-form slot="sandbox" :descriptor="[property.payload]" :payload="selectform" :onpropertychange="onpropertychange(selectform)"></cd-form>
    </cd-prop-example>
  </div>
</template>

<script>
import Vue from 'vue'
import CDInfo from '../generic/cd-doc-component-info.vue'
import CdDocGeneric from '../generic/cd-doc-generic.vue'
import inputype from '../common/inputtype'
import CDForm from '../components/cd-form.vue'
import CDSelect from '../components/cd-select.vue'
import CDPropExample from '../generic/cd-prop-example.vue'
import readyvaluesoptions from '../examples/propertyoptions'

const countryParams = {
  limit: 10,
  namePrefix: '',
  offset: 0
}

const cityParams = {
  limit: 10,
  minPopulation: null,
  namePrefix: null,
  countryIds: '',
  distanceUnit: null,
  offset: 0,
  excludedCountryIds: null
}

export default {
  components: { 'cd-doc': CdDocGeneric, 'cd-info': CDInfo, 'cd-form': CDForm, 'cd-prop-example': CDPropExample },
  name: 'cd-doc-home',
  data (home) {
    return {
      select: CDSelect.mixins.concat(CDSelect),
      content: [
        {
          id: 'home-1',
          header: 'CDJS',
          description: [
            'CDJS - Common Declarative JavaScript.'
          ],
          info: [
            {
              id: 'home-1-1',
              header: '',
              description: [
                '  .         '
              ],
              info: [
                {
                  header: '?',
                  description: [
                    '   ,     render-.'
                  ]
                }
              ]
            },
            {
              id: 'home-1-2',
              header: '',
              description: [
                '       . ,  ,      - ,     .<br/>',
                '         (   ).<br/>',
                ' ,       <br/>'
              ]
            },
            {
              id: 'home-1-3',
              header: '',
              description: [
                ' .  .    <br/>',
                ':    - ,   .<br/>',
                ' ,       , <code><b>datafield</b></code>,        <code><b>text</b></code> ,      ,            <br/>',
                '   ,   ,  ,             <br/>'
              ],
              info: [
                {
                  // header: ' <input>',
                  description: [
                    '        -   : <code><b>text</b></code>  <code><b>datafield</b></code><br/>'
                  ]
                },
                {
                  header: ' <code><b>type</b></code> -',
                  description: [
                    `   <code><b>input</b></code>    - <b>type</b>,      : [${inputype}]<br/>`,
                    '    <code><b>type</b></code>  ,      <code><b>input</b></code><br/>',
                    '...  - ....'
                  ]
                },
                {
                  header: ' <code><b>select</b></code> -',
                  description: [
                    '   <code><b>select</b></code>    .</br>',
                    `<pre>
<code>
datafield: String,
text: String,
valuekey: String, //  ,     selecta
labelkey: String, //  ,     
url: String, // ,    
method: String, // 
resolveresult: Function, //  (response) => Array,       axios 
isdisabled: Function, //  (payload, option) => Boolean,   option   datafield  payload
params: Function // ,      url    
</code>
</pre>`
                  ]
                }
              ]
            }
          ]
        }
      ],
      examples: [{
        id: 1,
        name: '    ',
        payload: {
          datafield: 'property1',
          text: ' -',
          labelkey: 'labelkey',
          valuekey: 'valuekey',
          input: 'select',
          values: [{ labelkey: 'label key 1', valuekey: 1 }, { labelkey: 'label key 2', valuekey: 2 }, { labelkey: 'label key 3', valuekey: 3 }]
        },
        paramsdescriptor: []
      }, {
        id: 2,
        name: ' ,    url',
        payload: {
          datafield: 'wikiDataId',
          text: '',
          input: 'select',
          valuekey: 'wikiDataId',
          labelkey: 'name',
          url: 'https://wft-geo-db.p.rapidapi.com/v1/geo/countries',
          method: 'get',
          resolveresult: (response) => response.data.data,
          resolvepayload: home.resolveCountryPayload,
          isdisabled: (payload, option) => option.wikiDataId.endsWith(7)
        },
        params: countryParams,
        paramsdescriptor: [
          {
            datafield: 'limit',
            text: 'limit'
          },
          {
            datafield: 'namePrefix',
            text: 'namePrefix'
          }
        ]
      }, {
        id: 3,
        name: '  ,    url',
        payload: {
          datafield: 'id',
          text: '',
          url: 'https://wft-geo-db.p.rapidapi.com/v1/geo/cities',
          method: 'get',
          input: 'select',
          labelkey: 'city',
          valuekey: 'id',
          resolveresult: (response) => response.data.data,
          resolvepayload: home.resolveCityPayload
        },
        paramsdescriptor: [
          {
            datafield: 'offset',
            text: 'offset'
          },
          {
            datafield: 'namePrefix',
            text: 'namePrefix'
          },
          {
            datafield: 'wikiDataId',
            text: '',
            input: 'select',
            valuekey: 'wikiDataId',
            labelkey: 'name',
            url: 'https://wft-geo-db.p.rapidapi.com/v1/geo/countries',
            method: 'get',
            resolveresult: (response) => response.data.data,
            resolvepayload: home.resolveCountryPayload,
            isdisabled: (payload, option) => option.wikiDataId.endsWith(7),
            onselect: (payload, option) => {
              Vue.set(payload, 'countryIds', `${option.code},`)
            }
          },
          {
            datafield: 'countryIds',
            text: 'countryIds'
          }
        ],
        params: cityParams
      }],
      property: false,
      selected: 1,
      options: readyvaluesoptions,
      selectform: {
        property1: 1
      },
      payload: false
    }
  },
  watch: {
    selected: {
      immediate: true,
      handler (newvalue) {
        if (newvalue) {
          this.property = this.examples.find(e => e.id === newvalue)
        }
      }
    }
  },
  methods: {
    onpropertyselect (event) {
      this.selected = Number(event.target.value)
    },
    onpropertychange (payload) {
      return (property, value) => {
        Vue.set(payload, property.datafield, value)
        // todo: after property changed
      }
    },
    resolveCityPayload (payload) {
      return {
        params: this.property.params
      }
    },
    resolveCountryPayload (payload) {
      return {
        params: this.property.params
      }
    }
  }
}
</script>

