export default {
  name: 'collection',
  props: {
    /**
     *   
     *    ,   -    
     * 
     *   resolveresult  - 
     */
    resolvepayload: {
      type: Function,
      returns: Object,
      description: '   payload,     .     ',
      default: (payload) => (payload)
    },
    /**
     * ,       
     */
    resolveresult: {
      type: Function,
      returns: Array,
      description: '    ,      '
    },
    /**
     * , 
     */
    collection: {
      type: Array,
      default: () => ([]),
      description: ' ,  '
    },
    /**
    * payload: ,      
    */
    payload: {
      type: Object,
      description: '   ,    ,       - ,   -     '
    },
    /**
      * keyfield:     
      *
      */
    keyfield: { type: String, required: true, description: '   ,    ,        ,  ,   ' },
    /**
     *   ,  , 
     *   get, remove, add, update
     *  ,   ,     ,
     *        , . watch.payload
     */
    // crud: { type: [Object, Function], description: '!!!!!    !!!!!       : get, update, delete, add { method: String, url: String },   , ,     .   method    http  (post, get...)   url   ' },
    get: { type: Object, description: ',     ' },
    /**
     * ,    
     *    payload  resolve
     */
    createnew: { type: Function, returns: Object, description: ',  ,     ' },
    /**
     * ,     ,
     *    
     *   ,   - ,     
     */
    onedit: { type: Function },
    /**
     * ,        
     */
    addnewheader: { type: String, default: '  ', description: ',      ' },
    /**
     *    
     */
    paging: { type: Boolean, default: false, description: '   ' },
    /**
     * - ,      
     */
    inner: { type: Boolean, default: false, description: ' ,   ' }
  },
  watch: {
    /**
     * , 
     *      ()
     *   handler
     */
    payload: {
      deep: true,
      immediate: true,
      /**
       *
       * @param {Object} newvalue    payload
       */
      handler (newvalue) {
        const local = this

        if ((newvalue && typeof newvalue !== 'function') && (local.$http) && (local.get && local.get.url !== '')) {
          local.loaddata(local.get.url, newvalue)
        }
      }
    }
  },
  computed: {
    //    
    //     crud    payload,   
    //  payload   ,    crud
    // urls () {
    //   if (typeof this.crud === 'function') return this.crud(this.payload)
    //   return this.crud
    // },
    rowkey () {
      const local = this
      return (row) => row[local.keyfield]
    }
  },
  data (col) {
    return {
      error: false
    }
  },
  methods: {
    loaddata (url, payload) {
      const local = this
      const request = local.$http[local.get.method]
      request(url, local.resolvepayload(payload))
        .then((response) => {
          local.resolveresult(response)
          local.error = false
        })
        .catch((reason) => {
          local.error = reason
        })
    },
    delete (row) {
      console.log('Deleting ', this.urls.remove, row)
    },
    update (row) {
      console.log('Updating ', this.urls.update, row)
    },
    callcreate () {
      const local = this
      const create = new Promise((resolve) => {
        local.createnew(local.payload, resolve)
      }).then((newvalue) => {
        local.onedit(newvalue, (payload) => {
          local.$http(local.urls.add, payload)
            .then((response) => {
              if (Object.prototype.hasOwnProperty.call(response, 'success')) {
                if (response.success) {
                  local.collection.push(response.data)
                } else if (Object.prototype.hasOwnProperty.call(response, 'message')) {
                  console.error(response.message)
                } else {
                  console.error('something goes wrong')
                }
              } else {
                console.info(response)
              }
            })
            .catch((reason) => {
              console.error(reason)
            })
        })
      })
      return create
    }
  }
}
